
import { defineComponent, watch, ref, reactive } from "vue";
import { routes } from "@/router/index";
import { RouteRecordRaw, RouteRecord } from "vue-router";
import { useMainStore } from "@/store/index";
import {
  MenuFoldOutlined,
  MenuUnfoldOutlined,
  PieChartOutlined,
  MailOutlined,
  DesktopOutlined,
  InboxOutlined,
  AppstoreOutlined,
} from "@ant-design/icons-vue";
const silderBar = defineComponent({
  component: [
    MenuFoldOutlined,
    MenuUnfoldOutlined,
    PieChartOutlined,
    MailOutlined,
    DesktopOutlined,
    InboxOutlined,
    AppstoreOutlined,
  ],
  setup() {
    console.log("routes", routes);
    const mainStore = useMainStore();
    let filter = ref<Array<RouteRecordRaw> | undefined>([]);
    watch(
      mainStore,
      (state) => {
        console.log("stata", state);
        filter.value = routes.filter(
          (i) => i.path === state.routerKey
        )[0].children;
      },
      {
        immediate: true,
      }
    );

    return {
      filter,
    };
  },
});

export default silderBar;
